html,
body,
.pyg_view {
  height: 100%;
}
img {
  width: 100%;
}
body {
  padding-top: 45px;
  padding-bottom: 45px;
}
.pyg_view {
  overflow: scroll;
}
.pyg_header {
  padding: 5px;
  width: 100%;
  height: 45px;
  position: fixed;
  background-color: #fff;
  top: 0;
  left: 0;
}
.pyg_footer {
  position: fixed;
  width: 100%;
  height: 45px;
  bottom: 0;
  left: 0;
  background-color: #fff;
  display: flex;
}
.pyg_footer a {
  flex: 1;
  text-align: center;
  color: #000;
  font-size: 14px;
  padding-top: 5px;
  border-top: 1px solid #d6d6d6;
}
.pyg_footer a > i {
  font-size: 18px;
}
.loadding::before {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  opacity: 0.5;
  content: "";
  z-index: 100;
}
.loadding::after {
  font: normal normal normal 14px/1 FontAwesome;
  position: fixed;
  top: 40%;
  left: 50%;
  margin-left: -50px;
  font-size: 100px;
  color: #0094ff;
  content: "\f110";
  z-index: 101;
  animation: fa-spin 1s infinite linear;
}
@media screen and (width: 375px) {
  html {
    font-size: 18.75px;
  }
}
@media screen and (width: 750px) {
  html {
    font-size: 37.5px;
  }
}
.pyg_view {
  display: flex;
  overflow: hidden;
}
.pyg_view .left_box {
  width: 5.33333333rem;
  overflow: hidden;
}
.pyg_view .left_box .left_menu {
  padding: 0;
  margin: 0;
  list-style: none;
}
.pyg_view .left_box .left_menu li {
  height: 2.13333333rem;
  font-size: 0.8rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.pyg_view .left_box .left_menu .active {
  background-color: #fff;
  border-left: 0.26666667rem solid #0094ff;
}
.pyg_view .right_box {
  flex: 1;
  overflow: hidden;
}
.pyg_view .right_box .rigth_item .item_title {
  height: 2.13333333rem;
  display: flex;
  padding-left: 0.8rem;
  align-items: center;
  font-size: 0.74666667rem;
  color: #0094ff;
}
.pyg_view .right_box .rigth_item .item_list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
}
.pyg_view .right_box .rigth_item .item_list li {
  width: 33.33%;
  text-align: center;
}
.pyg_view .right_box .rigth_item .item_list li a {
  font-size: 0.69333333rem;
  color: #000;
}
.pyg_view .right_box .rigth_item .item_list li a img {
  width: 80%;
}
